<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i> 基础表格
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-input v-model="keyword" placeholder="请输入关键字" clearable prefix-icon="el-icon-search"
                          class="handle-input"></el-input>
                <el-button type="primary" @click="getData()">搜索</el-button>
            </div>
            <el-table
                :data="tableData"
                border
                class="table"
                ref="multipleTable"
                header-cell-class-name="table-header"
                @selection-change="handleSelectionChange"
            >
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <el-table-column label="业绩报告编号" align="center" width="200">
                    <template slot-scope="scope">{{ scope.row.performanceId }}</template>
                </el-table-column>
                <el-table-column label="业绩提交时间" align="center" width="200">
                    <template slot-scope="scope">{{ scope.row.applyDate }}</template>
                </el-table-column>
                <el-table-column label="考勤天数" width="100" align="center">
                    <template slot-scope="scope">{{ scope.row.checkOnDay }}</template>
                </el-table-column>
                <el-table-column label="考勤天数占月份比重" width="100" align="center">
                    <template slot-scope="scope">{{ scope.row.checkOnPercent }}</template>
                </el-table-column>
                <el-table-column label="是否通过考核" width="100" align="center">
                    <template slot-scope="scope">{{ scope.row.performanceStatus }}</template>
                </el-table-column>
                <el-table-column label="是否申请考核复核" width="100" align="center">
                    <template slot-scope="scope">{{ scope.row.isReCheck }}</template>
                </el-table-column>
                <el-table-column label="复核情况" align="center">
                    <template slot-scope="scope">{{ scope.row.reCheckStatus }}</template>
                </el-table-column>
                <el-table-column label="操作" align="center">
                    <el-button slot-scope="scope" type="text" class="el-icon-edit"
                               v-if="scope.row.isReCheck == '是'" @click="handleReCheckStatus(scope.index,scope.row)">审核
                    </el-button>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                    background
                    layout="prev, pager, next ,->, total"
                    :page-count="pageTotal"
                    :current-page="query.pageIndex"
                    :page-size="query.size"
                    @current-change="handlePageChange"
                    @size-change="sizeChange"
                    @prev-click="handlePageChange"
                    @next-click="handlePageChange"
                ></el-pagination>
            </div>
            <el-dialog title="业绩审核" :visible.sync="editVisible" width="50%">
                <el-form ref="form" :model="performance" label-width="70px" enctype="multipart/form-data">
                    <el-form-item label="工号">
                        <el-input v-model="performance.workId" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="姓名">
                        <el-input v-model="performance.employee.fullname" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="部门">
                        <el-select v-model="performance.office" placeholder="请选择" disabled>
                            <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="提交时间">
                        <el-date-picker type="date" v-model="performance.applyDate" disabled></el-date-picker>
                    </el-form-item>
                    <el-form-item label="工作文件">
                        <el-upload
                            class="upload-demo"
                            action="#"
                            multiple
                            :on-change="handleChange"
                            :on-remove="handleRemove"
                            :http-request="submit()"
                            :file-list="fileList"
                            :auto-upload="false">
                            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                        </el-upload>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                <el-button  @click='saveAdd("通过")'>未通过</el-button>
                <el-button type="primary" @click='saveAdd("通过")'>通 过</el-button>
            </span>
            </el-dialog>
        </div>
    </div>
</template>

<script>
export default {
    name: "performanceReCheck",
    data() {
        return {
            performance: {
                workId: this.$store.state.user.workId,
                office: "",
                performanceId: "",
                applyDate: "",
                checkOnDay: "",
                checkOnPercent: "",
                performanceStatus: "",
                isReCheck: "",
                reCheckStatus: "",
                employee: {
                    fullname: ""
                }
            },
            query: {
                address: '',
                name: '',
                pageIndex: 1,
                size: 0
            },
            tableData: [],
            pageTotal: 0,
            total: 0,
            size: 5,
            page: 1,
            idx : -1,
            keyword: null,
            fileList: [],
            editVisible: false,
            options: [{
                value: '001',
                label: '行政部门'
            }, {
                value: '002',
                label: '人事部门'
            }, {
                value: '003',
                label: '销售部门'
            }, {
                value: '004',
                label: '财务部门'
            }, {
                value: '005',
                label: '美术部门'
            }, {
                value: '006',
                label: '策划部门'
            }],
        }
    },
    mounted() {
        this.getData()
    },
    methods: {
        getData() {
            let url = "oasys-back-end/getAllReCheck?page=" + this.page + "&size=" + this.size + "&office=" + this.$store.state.user.workId
            this.getRequest(url).then(resp => {
                if (resp.data.data != null) {
                    console.log(resp.data.data)
                    this.tableData = resp.data.data;
                    this.total = resp.data.total;
                    this.pageTotal = Math.ceil(this.total / this.size)
                } else {
                    this.$message.warning("没有数据")
                }
            })
        },
        handleReCheckStatus(index,row) {
            this.index = index
            this.performance = row
            this.editVisible = true
        },
        saveAdd(param) {
            this.performance.reCheckStatus = param
            this.postRequest('oasys-back-end/editReCheckStatus',this.performance).then(resp=>{
                if (resp.data === true){
                    this.$message.success("修改成功")
                }else{
                    this.$message.error("修改失败")
                }
            })
        },
        // 分页导航
        handlePageChange(currentPage) {
            this.page = currentPage;
            this.getData();
        },
        sizeChange(currentSize) {
            this.size = currentSize;
            this.getData();
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
            //console.log(this.multipleSelection.form)
        },
        submit(item) {

        },
        submitUpload() {
            let formData = new FormData()
            this.fileList.forEach(file => {
                formData.append('file', file.raw)
            })
            console.log(formData)
            this.fileRequest('oasys-back-end/upload', formData).then(resp => {
                if (resp) {
                    console.log("success")
                }
            })

        },
        handleRemove(file, fileList) {
            console.log(file, fileList);
        },
        handleChange(file, fileList) {
            this.fileList = fileList
            console.log(fileList)
        },
        handleReCheck: function () {
            console.log("测试")
        }
    }
}
</script>

<style scoped>
.handle-box {
    margin-bottom: 20px;
}

.handle-select {
    width: 120px;
}

.handle-input {
    width: 300px;
    display: inline-block;
}

.table {
    width: 100%;
    font-size: 14px;
}

.red {
    color: #ff0000;
}

.mr10 {
    margin-right: 10px;
}

.table-td-thumb {
    display: block;
    margin: auto;
    width: 40px;
    height: 40px;
}
</style>



